const showPopup = (triggerBtn, popup, closePopup, popupSucess, btnSucsess) => {
	const btn = document.querySelectorAll(triggerBtn);
	const pop = document.querySelector(popup);
	const closePop = document.querySelectorAll(closePopup);
	const popSucces = document.querySelector(popupSucess);
	const btnSuc = document.querySelector(btnSucsess);

	function close(selector) {
		selector.style.display = 'none';
	}
	function succes() {
		btnSuc.addEventListener('click', (e) => {
			e.preventDefault();
			const job = document.querySelector('.popup__form-job');
			const name = document.querySelector('.popup__form-name');
			const tel = document.querySelector('.popup__form-tel');
			if (job.value !== '' && name.value !== '' && tel.value !== '') {
				pop.style.display = 'none';
				popSucces.style.display = 'flex';
			} else {
				job.style.borderColor = 'red';
			}
		});
	}

	function init() {
		btn.forEach((item) => {
			item.addEventListener('click', () => {
				pop.style.display = 'flex';
				succes();
			});
		});
		closePop.forEach((item) => {
			item.addEventListener('click', () => {
				if (pop.style.display !== 'none') {
					close(pop);
				} else if (popSucces.style.display !== 'none') {
					close(popSucces);
				}
			});
		});
		pop.addEventListener('click', (e) => {
			if (e.target === pop) {
				close(pop);
			}
		});
		popSucces.addEventListener('click', (e) => {
			if (e.target === popSucces) {
				close(popSucces);
			}
		});
	}

	init();
};

export default showPopup;
