const showMenu = (triggerMenu, menu) => {
	const btn = document.querySelector(triggerMenu);
	const mobMenu = document.querySelector(menu);

	btn.addEventListener('click', (e) => {
		if (e.target === mobMenu) {
			mobMenu.style.left = '-110vw';
		} else {
			mobMenu.style.left = '0';
		}
	});
};

export default showMenu;
