import './vendor';
import './helpers';
import showPopup from './modules/showPopup';
import animate from './modules/animate';
import showMenu from './modules/showMenu';

window.addEventListener('DOMContentLoaded', () => {
	showPopup('#popup', '.popup', '.popup__close', '.popup-succes', '#btnSucsess');
	animate();
	if (window.matchMedia('(max-width: 768px)').matches) {
		showMenu('.header__nav', '.header__nav-ul');
	} else {
		console.log('Mobile menu not active width < 768px');
	}
});
