const animate = () => {
	document.addEventListener('mousemove', (e) => {
		let amountMovedX = 60 * ((e.pageY + 1) / document.documentElement.clientWidth) - 60
		let amountMovedY = 60 * ((e.pageY + 1) / window.innerWidth) - 100;
		let all = amountMovedX + amountMovedY;

		let resultLeft = all + 250 + 'px'
		let resultRight = all + 150 + 'px';
		let resultCenter = all + 120 + 'px';

		document.querySelector('.animate-Left').style.cssText = `top: ${resultLeft}`;
		document.querySelector('.animate-Right').style.cssText = `top: ${resultRight}`;
		document.querySelector('.animate-Center-img ').style.cssText = `top: ${resultCenter}`;

	});
};

export default animate;
